/**
 *	Set base font size to 10px
 *	This makes calculating rem values very easy
 *	Just divide your pixel value by 10
 *	Need an rem value that matches 13px? 1.3rem
 *	Need an rem value that matches 27px? 2.7rem
 */
:root {
  font-size: 10px; }

/**
 *	Reset element margins
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0; }

h1 + * {
  margin-top: 2rem; }

h2 + * {
  margin-top: 2rem; }

h3 + * {
  margin-top: 2rem; }

h4 + * {
  margin-top: 2rem; }

h5 + * {
  margin-top: 1.5rem; }

h6 + * {
  margin-top: 1.5rem; }

p + * {
  margin-top: 1.5rem; }

ol + * {
  margin-top: 1.5rem; }

ul + * {
  margin-top: 1.5rem; }

* + h1 {
  margin-top: 3rem; }

* + h2 {
  margin-top: 3rem; }

* + h3 {
  margin-top: 3rem; }

* + h4 {
  margin-top: 3rem; }

* + h5 {
  margin-top: 3rem; }

* + h6 {
  margin-top: 3rem; }

/*
