/*************************************
WordPress Specifics
**************************************/
.alignleft, .alignright {
  display: inline-block; }

.alignleft {
  float: left;
  margin: 0 20px 5px 0; }

.alignright {
  float: right;
  margin: 0 0 5px 20px; }

.alignnone {
  margin: 5px 15px 15px 0; }

.aligncenter {
  display: block;
  margin: 15px auto; }

.wp-caption {
  background: rgba(0, 0, 0, 0.1);
  border: 1px rgba(0, 0, 0, 0.3) solid;
  padding: 15px;
  font-size: 0.85em;
  text-align: center; }

.wp-caption img {
  border: 1px rgba(0, 0, 0, 0.3) solid;
  margin: 0 auto 15px auto;
  max-width: 98.5%;
  width: auto; }

@media only screen and (max-width: 782px) {
  body #wpadminbar {
    position: fixed !important; } }

/*************************************
Main Project Files
**************************************/
:root {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin-top: 60px; }
  @media only screen and (min-width: 641px) {
    body {
      margin-top: 70px; } }
  @media only screen and (min-width: 1025px) {
    body {
      margin-top: 80px; } }
  body.expand-main-content, body.expand-footer {
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 60px); }
    @media only screen and (min-width: 641px) {
      body.expand-main-content, body.expand-footer {
        min-height: calc(100vh - 70px); } }
    @media only screen and (min-width: 1025px) {
      body.expand-main-content, body.expand-footer {
        min-height: calc(100vh - 80px); } }
  body.expand-main-content .main-content {
    flex-grow: 1; }
  body.expand-footer .site-footer {
    flex-grow: 1; }

@media screen and (max-device-width: 641px) {
  body {
    -webkit-text-size-adjust: 100%; } }

ul {
  padding: 0; }
  nav ul {
    list-style: none;
    list-style-position: inside; }

table thead tr th {
  border-bottom: 1px solid #D4D7DA;
  padding: 0.5rem;
  text-align: left; }

table tbody tr td {
  border-bottom: 1px solid #D4D7DA;
  padding: 0.5rem;
  text-align: left; }

blockquote {
  margin: 2rem 1rem; }
  blockquote::before {
    content: '\201C'; }
  blockquote::after {
    content: '\201D'; }
  blockquote p {
    font: inherit;
    display: inline;
    color: inherit; }

code {
  background: #000000;
  color: #ffffff;
  padding: 0.1rem 0.25rem; }

strong {
  font-weight: 600; }

video,
iframe {
  max-width: 100%; }

body {
  margin: 0 20px !important;
  background-color: #fff; }
